.map{width:100%;display:flex;justify-content:center;position:relative;transition:all .1s ease}.map::after{content:"";position:absolute;background:none;transition:all .1s ease;z-index:0;width:0%;height:0%}.map.disactive{transition:all .1s ease}.map.disactive::after{content:"Необходим подбор АБС";position:absolute;width:100%;height:100%;background:rgba(74,206,175,.7);z-index:10000;display:flex;justify-content:center;align-items:center;color:#232323;font-size:2rem;text-shadow:2px 2px 10px #fff;transition:all .1s ease}